.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.6);
	display: inline-block;
  }
  .modal .modal-main {
	position: fixed;

	height: auto;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
  }

  .display-block {
	display: block;
  }
  
  .display-none {
